<template>
  <div>
    <el-row>
      <el-col :span="24">
        <div class="toolbar">
          <div style="float: left ">
            <el-button plain class="el-icon-plus" @click="openDialog('add')"></el-button>
          </div>
          <div style="float: right;">
            <el-form label-position="right" label-width="80px" :model="userForm"
                     :inline="inline" class="toolbar-form">
              <el-form-item label="" prop="login_name">
                <el-input v-model="userForm.login_name" clearable></el-input>
              </el-form-item>
              <!--<el-form-item label="" prop="real_name">
                <el-input v-model="userForm.real_name"></el-input>
              </el-form-item>-->
              <el-form-item label="">
                <el-select v-model="userForm.flag" clearable placeholder="">
                  <el-option label="" value=""></el-option>
                  <el-option
                    v-for="item in flagList"
                    :key="item.value"
                    :label="item.label"
                    :value="item.value">
                  </el-option>
                </el-select>
              </el-form-item>
              <!--<el-form-item label="">
                <el-select v-model="userForm.role" clearable placeholder="">
                  <el-option label="" value=""></el-option>
                  <el-option
                    v-for="item in roles"
                    :key="item.role_id"
                    :label="item.role_name"
                    :value="item.role_id">
                  </el-option>
                </el-select>
              </el-form-item>-->
              <el-form-item>
                <el-button class="el-icon-search" type="primary" @click="selUser"></el-button>
              </el-form-item>
            </el-form>
          </div>
        </div>
      </el-col>
    </el-row>
    <section class="table">
      <el-table
        :data="roleData"
        @selection-change="handleSelectionChange">
        <el-table-column label="" width="120px">
          <template slot-scope="scope">
            <el-button type="text" icon="el-icon-edit" title="" @click="openDialog('edit',scope.$index, scope.row)"></el-button>
            <el-button type="text" icon="el-icon-delete"  title="" @click="delUser(scope.$index, scope.row)"></el-button>
            <el-button type="text" icon="el-icon-refresh" title="" @click="resetPwd(scope.$index, scope.row)"></el-button>
            <el-button type="text" icon="el-icon-circle-check-outline" title="" @click="resetFlag(scope.$index, scope.row)" :disabled="scope.row.flag !== '0'"></el-button>
          </template>
        </el-table-column>
        <el-table-column prop="login_name" label="" align="left"></el-table-column>
        <el-table-column prop="real_name" label="" align="left"></el-table-column>
        <el-table-column prop="role_name" label="" align="left"></el-table-column>
        <el-table-column prop="flag" label="" align="left" :formatter=renderFlag></el-table-column>
        <!--<el-table-column prop="failed_login_attempts" label="" align="left" :formatter="emptyCheck" min-width="85"></el-table-column>
        <el-table-column prop="lockout" label="" align="left" :formatter="emptyCheck"></el-table-column>-->
        <el-table-column prop="lockout_date" label="" align="left"></el-table-column>
        <el-table-column prop="email" label="" align="left" min-width="170"></el-table-column>
        <el-table-column prop="mobile" label="" align="left" min-width="85"></el-table-column>
      </el-table>
      <el-pagination style="margin-top: 10px; text-align: right;"
                     @size-change="handleSizeChange"
                     @current-change="handleCurrentChange"
                     :current-page="currentPage"
                     :page-sizes="[10, 25, 50, 100]"
                     :page-size="pagesize"
                     layout="total, sizes, prev, pager, next, jumper"
                     :total="total">
      </el-pagination>
    </section>
    <el-dialog :title="dialogTitle" :visible.sync="roleDialogVisible" width="900px" :close-on-click-modal="false">
      <el-form :model="userDialogForm" :rules="rules" ref="userDialogForm" :inline="true">
        <el-form-item label="" :label-width="formLabelWidth" prop="login_name" :style="formItemStyle">
          <el-input v-model="userDialogForm.login_name" auto-complete="off" :style="formItemContentStyle"></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth" prop="real_name" :style="formItemStyle">
          <el-input v-model="userDialogForm.real_name" auto-complete="off" :style="formItemContentStyle"></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth" prop="role" style="text-align: left;" :style="formItemStyle">
          <el-select v-model="userDialogForm.role" clearable placeholder="" :style="formItemContentStyle">
            <el-option
              v-for="item in roles"
              :key="item.role_id"
              :label="item.role_name"
              :value="item.role_id">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth" style="text-align: left;" :style="formItemStyle">
          <el-switch
            v-model="userDialogForm.flag"
            active-text=""
            inactive-text=""
            active-value="1"
            inactive-value="0">
          </el-switch>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth" prop="credentialtype" style="text-align: left;" :style="formItemStyle">
          <el-select v-model="userDialogForm.credentialtype" clearable placeholder="" :style="formItemContentStyle">
            <el-option
              v-for="item in credentialList"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth" prop="credentialnum" :style="formItemStyle">
          <el-input v-model="userDialogForm.credentialnum" auto-complete="off" :style="formItemContentStyle"></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth" prop="phone" :style="formItemStyle">
          <el-input v-model="userDialogForm.phone" auto-complete="off" :style="formItemContentStyle"></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth" prop="mobile" :style="formItemStyle">
          <el-input v-model="userDialogForm.mobile" auto-complete="off" :style="formItemContentStyle"></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth" prop="email" :style="formItemStyle">
          <el-input v-model="userDialogForm.email" auto-complete="off" :style="formItemContentStyle"></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth" prop="address" :style="formItemStyle">
          <el-input v-model="userDialogForm.address" auto-complete="off" :style="formItemContentStyle"></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth" prop="failed_login_attempts" :style="formItemStyle" v-if="userDialogForm.operator_id !==''">
          <el-input v-model="userDialogForm.failed_login_attempts" auto-complete="off" :style="formItemContentStyle" :readonly="true"></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth" prop="lockout" :style="formItemStyle" v-if="userDialogForm.operator_id !==''">
          <el-input v-model="userDialogForm.lockout" auto-complete="off" :style="formItemContentStyle" :readonly="true"></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth" prop="memo" :style="formItemStyle">
          <el-input type="textarea" v-model="userDialogForm.memo" :style="formItemContentStyle"></el-input>
        </el-form-item>
        <el-form-item label="1" :label-width="formLabelWidth" prop="repwd" style="display: none" :style="formItemStyle">
          <el-input v-model="userDialogForm.repwd" auto-complete="off" :style="formItemContentStyle"></el-input>
        </el-form-item>
        <el-form-item label="2" :label-width="formLabelWidth" prop="password" style="display: none" :style="formItemStyle">
          <el-input v-model="userDialogForm.password" auto-complete="off" :style="formItemContentStyle"></el-input>
        </el-form-item>

        <div>
          <el-form-item label=" " :label-width="formLabelWidth" :style="formItemStyle">
            <el-button type="primary" @click="submitForm('userDialogForm')" size="large"> </el-button>
            <el-button @click="roleDialogVisible = false" size="large"> </el-button>
          </el-form-item>
        </div>

      </el-form>
    </el-dialog>
  </div>
</template>

<script>
  import check from '@/common/check'
  import ajax from '@/common/ajax'
  import crypt from '@/common/crypt'
  import dictCode from '@/common/dictCode'

  export default {
    created () {
      this.selUser()
      this.selRole()
      this.credentialList = dictCode.getCodeItems('tms.certificate.type')
      this.flagList = dictCode.getCodeItems('cmc.cmc_operate.flag')
    },
    methods: {
      handleSizeChange (val) {
        this.currentPage = 1
        this.pageSize = val
        this.selUser()
      },
      handleCurrentChange (val) {
        this.currentPage = val
        this.selUser()
      },
      handleSelectionChange (val) {
        this.multipleSelection = val
      },
      renderFlag (row, column, cellValue) {
        return dictCode.rendCode('cmc.cmc_operate.flag', cellValue)
      },
      resetForm (formName) {
        this.$refs[formName].resetFields()
      },
      submitForm (formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            if (this.flag === 'add') {
              this.addUser()
            } else if (this.flag === 'edit') {
              this.updateUser()
            }
          } else {
            // this.$message('')
            return false
          }
        })
      },
      bindGridData (data) {
        this.roleData = data.page.list
        this.currentPage = data.page.index
        this.pagesize = data.page.size
        this.total = data.page.total
      },
      selRole () {
        var self = this
        ajax.post({
          url: '/role/listNormalRole',
          success: function (data) {
            self.roles = data.row
          },
          fail: function (e) {
            if (e.response.data.message) {
              self.$message.error(e.response.data.message)
            }
          }
        })
      },
      selUser () {
        var self = this
        ajax.post({
          url: '/operator/list',
          param: {
            login_name: this.userForm.login_name,
            real_name: this.userForm.real_name,
            flag: this.userForm.flag,
            role: this.userForm.role,
            pageindex: self.currentPage,
            pagesize: self.pagesize
          },
          success: function (data) {
            if (data.page) {
              self.bindGridData(data)
            }
          },
          fail: function (e) {
            if (e.response.data.message) {
              self.$message.error(e.response.data.message)
            }
          }
        })
      },
      openDialog (flag, index, row) {
        let self = this
        this.flag = flag
        if (flag === 'edit') {
          this.dialogTitle = ''
          ajax.post({
            url: '/operator/get',
            param: {operatorId: row.operator_id},
            success: function (data) {
              var info = data.row
              self.multipleSelection = [info]
              self.userDialogForm = {
                operator_id: info.operator_id,
                login_name: info.login_name,
                flag: info.flag,
                real_name: info.real_name,
                role: info.role,
                credentialtype: info.credentialtype,
                credentialnum: info.credentialnum,
                phone: info.phone,
                mobile: info.mobile,
                email: info.email,
                address: info.address,
                memo: info.memo,
                repwd: info.password,
                password: info.password,
                failed_login_attempts: info.failed_login_attempts || 0,
                lockout: info.lockout || 0
              }
            },
            fail: function (e) {
              if (e.response.data.message) {
                self.$message.error(e.response.data.message)
              }
            }
          })
        } else if (flag === 'add') {
          this.dialogTitle = ''
          this.userDialogForm = {
            operator_id: '',
            login_name: '',
            flag: '1',
            real_name: '',
            role: '',
            credentialtype: '',
            credentialnum: '',
            phone: '',
            mobile: '',
            email: '',
            address: '',
            memo: '',
            repwd: crypt.md5('123456'),
            password: crypt.md5('123456')
          }
        }
        this.roleDialogVisible = true
        if (this.$refs['userDialogForm']) {
          this.$refs['userDialogForm'].clearValidate()
        }
      },
      addUser () {
        var self = this
        ajax.post({
          url: '/operator/add',
          param: this.userDialogForm,
          success: function (data) {
            self.$message.success('')
            self.roleDialogVisible = false
            self.selUser()
          },
          fail: function (e) {
            if (e.response.data.message) {
              self.$message.error(e.response.data.message)
            }
          }
        })
      },
      updateUser () {
        var self = this
        ajax.post({
          url: '/operator/mod',
          param: this.userDialogForm,
          success: function (data) {
            self.$message.success('')
            self.roleDialogVisible = false
            self.selUser()
          },
          fail: function (e) {
            if (e.response.data.message) {
              self.$message.error(e.response.data.message)
            }
          }
        })
      },
      delUser (index, row) {
        var self = this
        this.$confirm('', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          ajax.post({
            url: '/operator/del',
            param: [row.operator_id],
            success: function (data) {
              self.$message.success('')
              self.selUser()
            },
            fail: function (e) {
              if (e.response.data.message) {
                self.$message.error(e.response.data.message)
              }
            }
          })
        }).catch(() => {})
      },
      resetPwd (index, row) {
        var self = this
        var pwd = crypt.md5('123456') // MD5
        this.$confirm('', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          ajax.post({
            url: '/operator/reset',
            param: {
              operatorId: row.operator_id,
              loginName: row.login_name,
              passWord: pwd
            },
            success: function (data) {
              self.$message.success('')
              self.selUser()
            },
            fail: function (e) {
              if (e.response.data.message) {
                self.$message.error(e.response.data.message)
              }
            }
          })
        }).catch(() => {})
      },
      resetFlag (index, row) {
        var self = this
        this.$confirm('', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          ajax.post({
            url: '/operator/resetLoginFailedAttempts',
            param: {
              operatorId: row.operator_id
            },
            success: function (data) {
              if (data.result) {
                self.$message.success('')
              } else {
                self.$message.success('')
              }
              self.selUser()
            },
            fail: function (e) {
              if (e.response.data.message) {
                self.$message.error(e.response.data.message)
              }
            }
          })
        }).catch(() => {})
      },
      emptyCheck (row, item) {
        let val = row[item.property]
        return val || 0
      }
    },
    data () {
      //  6
      var UserNameCheck = (rule, value, callback) => {
        var type = /^[0-9a-zA-Z]*$/
        if (type.test(value)) {
          callback()
        } else {
          return callback(new Error('6-50'))
        }
      }
      // 
      var UserNameExist = (rule, value, callback) => {
        let self = this
        if (this.flag === 'edit' && this.multipleSelection[0].login_name === value) {
          callback()
        } else {
          ajax.post({
            url: '/operator/check/username',
            param: {'username': value}, // 
            success: function (data) { // 
              debugger
              if (data.checke_result === 'false') {
                return callback(new Error(''))
              } else {
                callback()
              }
            },
            fail: function (e) {
              if (e.response.data.message) {
                self.$message.error(e.response.data.message)
              }
            }
          })
        }
      }
      //  
      var credentialNumCheck = (rule, value, callback) => {
        var type = /^[0-9a-zA-Z]*$/
        if (type.test(value)) {
          callback()
        } else {
          return callback(new Error(''))
        }
      }
      // 
      var credentialTypeInputCheck = (rule, value, callback) => {
        var item = this
        if (value.trim() === '' && item.userDialogForm.credentialnum !== '') { // 
          return callback(new Error(''))
        } else if (value.trim() !== '' && item.userDialogForm.credentialnum !== '') { // 
          // item.$refs.userDialogForm.validateField('credentialnum')
          callback()
        } else {
          callback()
        }
      }
      // 
      var credentialNumInputCheck = (rule, value, callback) => {
        var item = this
        if (value.trim() === '' && item.userDialogForm.credentialtype !== '') { // 
          return callback(new Error(''))
        } else if (value.trim() !== '') { // 
          var credentialNum = item.userDialogForm.credentialtype.toString() // 
          if (credentialNum === '0') { // 
            var num = /^[1-9]\d{5}[1-9]\d{3}((0\d)|(1[0-2]))(([0|1|2]\d)|3[0-1])((\d{4})|\d{3}[A-Z])$/
            if (!num.test(credentialNum)) {
              return callback(new Error(''))
            }
          }
          item.$refs.userDialogForm.validateField('credentialtype')
          callback()
        } else {
          callback()
        }
      }
      var phoneCheck = (rule, value, callback) => {
        var type = /^(([0+]\d{2,3}-)?(0\d{2,3})-)?(\d{7,8})(-(\d{3,}))?$/
        if ((value.trim() === '') || (type.test(value))) {
          callback()
        } else {
          return callback(new Error(''))
        }
      }
      var mobileCheck = (rule, value, callback) => {
        var type = /(^13\d{9}$)|(^15\d{9}$)|(^18\d{9}$)/
        if (type.test(value)) {
          callback()
        } else {
          return callback(new Error(''))
        }
      }

      return {
        formItemStyle: {
          width: '400px'
        },
        formItemContentStyle: {
          width: '250px'
        },
        inline: true,
        roles: [],
        credentialList: [],
        flagList: [],
        userForm: {
          login_name: '',
          real_name: '',
          flag: '',
          role: ''
        },
        roleData: [],
        currentPage: 1,
        pagesize: 10,
        total: 0,
        roleDialogVisible: false,
        userDialogForm: {
          operator_id: '',
          login_name: '',
          flag: '1',
          real_name: '',
          role: '',
          credentialtype: '',
          credentialnum: '',
          phone: '',
          mobile: '',
          email: '',
          address: '',
          memo: '',
          repwd: '123456',
          password: '123456'
        },
        dialogTitle: '',
        formLabelWidth: '100px',
        rules: {
          login_name: [
            {required: true, message: '', trigger: 'blur'},
            {min: 6, max: 50, message: '6-50', trigger: 'blur'},
            {validator: UserNameCheck, trigger: 'blur'}, // 
            {validator: UserNameExist, trigger: 'blur'} // 
          ],
          real_name: [
            {required: true, message: '', trigger: 'blur'},
            {max: 50, message: '50', trigger: 'blur'},
            {validator: check.checkFormSpecialCode, trigger: 'blur'} // 
          ],
          role: [
            {required: true, message: '', trigger: 'blur'}
          ],
          credentialtype: [
            {validator: credentialTypeInputCheck, trigger: 'blur'} // 
          ],
          credentialnum: [
            {max: 50, message: '32', trigger: 'blur'},
            {validator: credentialNumCheck, trigger: 'blur'}, // 
            {validator: credentialNumInputCheck, trigger: 'blur'} // 
          ],
          phone: [
            {validator: phoneCheck, trigger: 'blur'} // ,,
          ],
          mobile: [
            {required: true, message: '', trigger: 'blur'},
            {validator: mobileCheck, trigger: 'blur'} // 
          ],
          email: [
            {required: true, message: '', trigger: 'blur'},
            {type: 'email', message: '', trigger: 'blur'},
            {max: 50, message: '50', trigger: 'blur'}
          ],
          address: [
            {max: 200, message: '200', trigger: 'blur'},
            {validator: check.checkFormSpecialCode, trigger: 'blur'} // 
          ],
          memo: [
            {max: 300, message: '300', trigger: 'blur'},
            {validator: check.checkFormSpecialCode, trigger: 'blur'} // 
          ]
        },
        multipleSelection: [],
        flag: ''
      }
    }
  }
</script>

<style>

</style>



