<template>
  <div>
    <transition name="fade">
      <div class="more-query" v-show="queryFormShow">
        <el-form label-position="right" label-width="120px" :model="queryShowForm" ref="queryShowForm" :rules="queryRules"
                 :inline="true">
          <el-form-item label=":" prop="txncode">
            <el-input v-model="queryShowForm.txncode" class="alarm-event-query-form-item" auto-complete="off" :maxlength="32" clearable></el-input>
          </el-form-item>
          <el-form-item label=":" prop="userid">
            <el-input v-model="queryShowForm.userid" class="alarm-event-query-form-item" auto-complete="off" :maxlength="32" clearable></el-input>
          </el-form-item>
          <el-form-item label=":" prop="username">
            <el-input v-model="queryShowForm.username" class="alarm-event-query-form-item" auto-complete="off" :maxlength="32" clearable></el-input>
          </el-form-item>
          <el-form-item label="IP:" prop="ipaddr">
            <el-input v-model="queryShowForm.ipaddr" class="alarm-event-query-form-item" auto-complete="off" :maxlength="32" clearable></el-input>
          </el-form-item>
          <el-form-item label=":" prop="deviceid">
            <el-input v-model="queryShowForm.deviceid" class="alarm-event-query-form-item" auto-complete="off" :maxlength="32" clearable></el-input>
          </el-form-item>
          <el-form-item label=":" prop="sessionid">
            <el-input v-model="queryShowForm.sessionid" class="alarm-event-query-form-item" auto-complete="off" :maxlength="32" clearable></el-input>
          </el-form-item>
          <el-form-item label=":" prop="txntype">
            <div @click="openTxnTypedialog" >
              <el-input v-model="queryShowForm.txntype" class="alarm-event-query-form-item" auto-complete="off" readonly clearable></el-input>
            </div>
          </el-form-item>
          <el-form-item label=":" prop="disposal">
            <el-select v-model="queryShowForm.disposal" class="alarm-event-query-form-item" placeholder=""
                       clearable>
              <el-option
                v-for="item in disposalList"
                :key="item.dp_code"
                :label="item.dp_name"
                :value="item.dp_code">
              </el-option>

            </el-select>
          </el-form-item>
          <el-form-item label=":" prop="iseval">
            <el-select v-model="queryShowForm.iseval" class="alarm-event-query-form-item" placeholder=""
                       clearable>
              <el-option
                v-for="item in isAssessmentList"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>

            </el-select>
          </el-form-item>
          <el-form-item label=":" prop="iscorrect">
            <el-select v-model="queryShowForm.iscorrect" class="alarm-event-query-form-item" placeholder=""
                       clearable>
              <el-option
                v-for="item in isCorrectList"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label=":" prop="confirmrisk">
            <el-select v-model="queryShowForm.confirmrisk" class="alarm-event-query-form-item" placeholder=""
                       clearable>
              <el-option
                v-for="item in confirmRiskList"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label=":" prop="txnstatus">
            <el-select v-model="queryShowForm.txnstatus" class="alarm-event-query-form-item" placeholder=""
                       clearable>
              <el-option
                v-for="item in txnStatusList"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label=":" prop="txntime">
            <el-date-picker
              v-model="queryShowForm.txntime"
              type="datetimerange"
              range-separator=""
              start-placeholder=""
              end-placeholder="">
            </el-date-picker>
          </el-form-item>
          <el-form-item label=":" prop="countrycode">
            <el-select v-model="queryShowForm.countrycode" class="alarm-event-query-form-item" placeholder="" clearable filterable>
              <el-option
                v-for="item in countryCodeList"
                :key="item.countrycode"
                :label="item.countryname"
                :value="item.countrycode">
              </el-option>
            </el-select>
            <el-select v-model="queryShowForm.regioncode" class="alarm-event-query-form-item" placeholder="" clearable filterable>
              <el-option
                v-for="item in regionCodeList"
                :key="item.regioncode"
                :label="item.regionname"
                :value="item.regioncode">
              </el-option>
            </el-select>
            <el-select v-model="queryShowForm.citycode" class="alarm-event-query-form-item" placeholder="" clearable filterable>
              <el-option
                v-for="item in cityCodeList"
                :key="item.citycode"
                :label="item.cityname"
                :value="item.citycode">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item style="float: right">
            <el-button type="primary" @click="queryFormShow = !queryFormShow"></el-button>
            <el-button type="primary" @click="searchData"></el-button>
          </el-form-item>
        </el-form>
      </div>
    </transition>
    <div class="toolbar" v-show="!queryFormShow">
      <el-form label-position="right" label-width="120px" :model="queryShowForm" ref="queryShowForm" :rules="queryRules"
               :inline="true" class="toolbar-form">
        <el-form-item label=":" prop="txntime" >
          <el-date-picker
            v-model="queryShowForm.txntime"
            type="datetimerange"
            range-separator=""
            start-placeholder=""
            end-placeholder="">
          </el-date-picker>
        </el-form-item>
        <el-form-item label=":" prop="txncode">
          <el-input v-model="queryShowForm.txncode" class="alarm-event-query-form-item" auto-complete="off" :maxlength="32" clearable></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="queryFormShow = !queryFormShow"></el-button>
          <el-button type="primary" class="el-icon-search" @click="searchData"></el-button>
        </el-form-item>
      </el-form>
    </div>

    <section class="section">
      <el-table :data="tnxEventTableData">
        <el-table-column prop="txncode" label="" align="left" fixed min-width="120px" :show-overflow-tooltip="true">
          <template slot-scope="scope">
            <a @click="queryTxnInfo(scope.row, 'operate')" href="javascript:void(0)">{{ scope.row.txncode }}</a>
          </template>
        </el-table-column>
        <el-table-column prop="userid" label="" align="left" fixed min-width="115px" :show-overflow-tooltip="true">
          <template slot-scope="scope">
            <a @click="queryTxnInfo(scope.row, 'user')" href="javascript:void(0)">{{ scope.row.userid }}</a>
          </template>
        </el-table-column>
        <el-table-column prop="sessionid" label="" align="left" min-width="100px" :show-overflow-tooltip="true">
          <template slot-scope="scope">
            <a @click="queryTxnInfo(scope.row, 'session')" href="javascript:void(0)">{{ scope.row.sessionid }}</a>
          </template>
        </el-table-column>
        <el-table-column prop="txnname" label="" align="left" min-width="100px"></el-table-column>
        <el-table-column prop="txntime" label="" align="left" :formatter="renderDateTime" min-width="100px" :show-overflow-tooltip="true"></el-table-column>
        <el-table-column prop="location" label="" align="left"  min-width="100px"></el-table-column>
        <el-table-column prop="deviceid" label="" align="left">
          <template slot-scope="scope">
            <a @click="queryTxnInfo(scope.row, 'device')" href="javascript:void(0)">{{ scope.row.deviceid }}</a>
          </template>
        </el-table-column>
        <el-table-column prop="ipaddr" label="IP" align="left" min-width="112px"></el-table-column>
        <el-table-column prop="iseval" label="" align="left" :formatter="renderIsEval"></el-table-column>
        <el-table-column prop="ismodelrisk" label="" align="left" :formatter="renderIsModelRisk"></el-table-column>
        <el-table-column prop="hitrulenum" label="" align="left">
          <template slot-scope="scope">
            <a @click="queryTxnInfo(scope.row, 'strategy')" href="javascript:void(0)">{{ scope.row.hitrulenum }}</a>
          </template>
        </el-table-column>
        <el-table-column prop="score" label="" align="left"></el-table-column>
        <el-table-column prop="iscorrect" label="" align="left" :formatter="renderIsCorrect"></el-table-column>
        <el-table-column prop="disposal" label="" align="left"></el-table-column>
        <el-table-column prop="confirmrisk" label="" align="left" :formatter="renderConfirmRisk" min-width="105px"></el-table-column>
        <el-table-column prop="txnstatus" label="" align="left" :formatter="renderTxnStatus"></el-table-column>
      </el-table>
      <el-pagination style="margin-top: 10px; text-align: right;"
                     :current-page="currentPage"
                     @size-change="handleSizeChange"
                     @current-change="handleCurrentChange"
                     :page-sizes="[10, 25, 50, 100]"
                     :page-size="pageSize"
                     layout="total, sizes, prev, pager, next, jumper"
                     :total="total">
      </el-pagination>
    </section>

    <el-dialog title="" :visible.sync="txntypeDialogVisible" width="400px" >
      <el-tree :data="treeData" node-key="id" ref="tree"
               show-checkbox
               :props="defaultTreeProps"
               :highlight-current=true
               :default-expanded-keys="expendKey"
               :expand-on-click-node="false"
               :render-content="renderContent"
               @check-change="handleCheckChange"
               style="overflow-y: auto;">
      </el-tree>

      <div slot="footer" class="dialog-footer">
        <el-button @click="txntypeDialogVisible = false" size="large"></el-button>
      </div>
    </el-dialog>
    <txn-detail ref="txnDetail" :txn="selectedRow" :defaultTab="defaultTab" :showItem=showItem></txn-detail>
  </div>
</template>

<script>
  import ajax from '@/common/ajax'
  import util from '@/common/util'
  import dictCode from '@/common/dictCode'
  import check from '@/common/check'
  import TxnDetail from '@/components/synquery/TxnDetail'
  /**
   * 
   */
  let isCorrectList = [{'label': '', 'value': '2'}, {'label': '', 'value': '1'}, {'label': '', 'value': '0'}]
  /**
   * 
   * @type {*[]}
   */
  let isAssessmentList = [{label: '', value: 0}, {label: '', value: 1}]

  export default {
    data () {
      // 
      let txnTimeCheck = (rule, value, callback) => {
        let self = this
        let txncode = self.queryShowForm.txncode
        if (!txncode) {
          if (value && value.length === 2) {
            let startTime = value[0]
            let endTime = value[1]
            let range = (endTime - startTime) / 86400000

            let userid = self.queryShowForm.userid
            let username = self.queryShowForm.username
            let ipaddr = self.queryShowForm.ipaddr
            let deviceid = self.queryShowForm.deviceid
            let sessionid = self.queryShowForm.sessionid

            if (userid || username || ipaddr || deviceid || sessionid) {
              if (range > 30) {
                return callback(new Error('1(30)'))
              }
            } else {
              if (range > 1) {
                return callback(new Error('1'))
              }
            }
          } else {
            return callback(new Error(''))
          }
        }
        callback()
      }
      return {
        txntypeDialogVisible: false,
        defaultTreeProps: {
          children: 'children',
          label: 'tab_desc'
        },
        expendKey: ['T'], // id
        treeData: [],
        tnxEventTableData: [],    // 
        queryFormShow: false,  // 
        queryShowForm: {      // 
          ruleid: '',
          txncode: '',        // 
          userid: '',         // 
          username: '',       // 
          ipaddr: '',         // IP
          deviceid: '',       // 
          sessionid: '',      // 
          txntype: '',        // 
          disposal: '',       // 
          iseval: '',         // 
          iscorrect: '',      // 
          confirmrisk: '',    // 
          txnstatus: '',      // 
          txntime: [],        // 
          operate_time: '',   // 
          end_time: '',       // 
          location: '',       // 
          countrycode: '',    // 
          regioncode: '',     // 
          citycode: ''        // 
        },
        isAssessmentList: isAssessmentList, // 
        isCorrectList: isCorrectList, // 
        confirmRiskList: [],  // 
        countryCodeList: [],  // 
        regionCodeList: [],   // 
        cityCodeList: [],     // 
        txnStatusList: [],    // 
        disposalList: [],     // 
        currentPage: 1,       // 
        pageSize: 10,         // 
        total: 0,             // 
        selectedRow: {},      // 
        showItem: ['operate', 'strategy', 'count', 'user', 'device', 'deviceFinger', 'session'],
        defaultTab: '',
        queryRules: {         // 
          txncode: [
            {max: 32, message: '32', trigger: 'blur'}
          ],
          userid: [
            {max: 32, message: '32', trigger: 'blur'}
          ],
          username: [
            {max: 32, message: '32', trigger: 'blur'}
          ],
          ipaddr: [
            {max: 16, message: '16', trigger: 'blur'}, // ip
            {validator: check.checkFormIp, trigger: 'blur'}
          ],
          deviceid: [
            {max: 32, message: '32', trigger: 'blur'}
          ],
          sessionid: [
            {max: 64, message: '64', trigger: 'blur'}
          ],
          txntime: [
            {validator: txnTimeCheck, trigger: 'blur'} // 
          ]
        }
      }
    },
    created () {
      let self = this
      let txntime = self.initDateRange()
      // txntime = [new Date('2018/1/5'), new Date('2018/1/6')]
      self.queryShowForm.txntime = txntime
      self.getTxnEventTableData({
        operate_time: txntime[0].getTime(),
        end_time: txntime[1].getTime(),
        pageindex: 1,
        pagesize: self.pageSize
      })
    },
    mounted: function () {
      this.$nextTick(function () {
        let self = this
        self.txnStatusList = dictCode.getCodeItems('tms.common.txnstatus')
        self.confirmRiskList = dictCode.getCodeItems('tms.confirmrisk.status')
        this.selTree()
        ajax.post({
          url: '/monitor/alarm/get_all_country',
          param: {},
          success: function (data) {
            self.countryCodeList = data.row
          }
        })
        ajax.post({
          url: '/rule/disposal',
          param: {},
          success: function (data) {
            self.disposalList = data.row
          }
        })
      })
    },
    watch: {
      'queryShowForm.txntime': function (val) {
        let startTime = ''
        let endTime = ''
        if (val && val.length === 2) {
          startTime = val[0].getTime()
          endTime = val[1].getTime()
        }
        this.queryShowForm.operate_time = startTime
        this.queryShowForm.end_time = endTime
      },
      'queryShowForm.countrycode': function (val) {
        let self = this
        ajax.post({
          url: '/monitor/alarm/get_region_by_country',
          param: {country: val},
          success: function (data) {
            self.regionCodeList = data.row
          }
        })
      },
      'queryShowForm.regioncode': function (val) {
        let self = this
        ajax.post({
          url: '/monitor/alarm/get_city_by_region',
          param: {region: val},
          success: function (data) {
            self.cityCodeList = data.row
          }
        })
      }
    },
    methods: {
      bindGridData (data) {
        this.tnxEventTableData = data.page.list
        this.currentPage = data.page.index
        this.pageSize = data.page.size
        this.total = data.page.total
      },
      handleSizeChange (val) {
        // console.log(` ${val} `)
        this.currentPage = 1
        this.pageSize = val
        this.getTxnEventTableData()
      },
      handleCurrentChange (val) {
        this.currentPage = val
        this.getTxnEventTableData()
      },
      getTxnEventTableData (params) {
        let self = this
        let param = params || Object.assign({
          pageindex: this.currentPage,
          pagesize: this.pageSize
        }, self.queryShowForm)
        ajax.post({
          url: '/query/tnxEvent/result',
          param: param,
          success: function (data) {
            if (data.success === true) {
              self.bindGridData(data)
            }
          },
          fail: function (rep) {
            console.error('', rep)
          },
          error: function (e) {
            console.error('', e)
          }
        })
      },
      searchData () {
        this.$refs['queryShowForm'].validate((valid) => {
          if (valid) {
            let params = Object.assign({
              pageindex: 1,
              pagesize: this.pageSize
            }, this.queryShowForm)
            this.getTxnEventTableData(params)
          } else {
            this.$message('')
            return false
          }
        })
      },
      initDateRange () {
        let start = new Date()
        start.setHours(new Date().getHours() - 1)
        let end = new Date()
        return [start, end]
      },
      renderDateTime (row, column, cellValue) {
        return util.renderDateTime(cellValue)
      },
      renderIsEval (row, column, cellValue) {
        switch (cellValue) {
          case '0':
            return ''
          case '1':
            return ''
          default:
            return ''
        }
      },
      renderIsModelRisk (row, column, cellValue) {
        switch (cellValue) {
          case '0':
            return ''
          case '1':
            return ''
          default:
            return ''
        }
      },
      renderIsCorrect (row, column, cellValue) {
        switch (cellValue) {
          case '0':
            return ''
          case '1':
            return ''
          case '2':
            return ''
          default:
            return ''
        }
      },
      renderConfirmRisk (row, column, cellValue) {
        return dictCode.rendCode('tms.confirmrisk.status', cellValue)
      },
      renderTxnStatus (row, column, cellValue) {
        return dictCode.rendCode('tms.common.txnstatus', cellValue)
      },
      queryTxnInfo (row, defaultTab) {
        let self = this
        self.selectedRow = row
        self.defaultTab = defaultTab
        let num = 0
        let openTimer = window.setInterval(function () {
          if (defaultTab === self.defaultTab) {
            window.clearInterval(openTimer)
            self.$refs['txnDetail'].open()
          }
          if (num > 10) {
            self.$message('')
          }
          num++
        }, 100)
      },
      openTxnTypedialog () {
        this.txntypeDialogVisible = true
      },
      handleCheckChange (data, checked, indeterminate) {
        let checkedArr = this.$refs.tree.getCheckedNodes()
        let checkedStrArr = []
        let checkedStrKeyArr = []
        for (let item of checkedArr) {
          checkedStrArr.push(item.tab_desc)
          checkedStrKeyArr.push(item.tab_name)
        }
        this.queryShowForm.txntype = checkedStrKeyArr.join(',')
      },
      // 
      renderContent (h, { node, data, store }) {
        if (node.data.enable === 0) { // 
          return (<span class="el-tree-node__label disabledFlag">{node.label}</span>)
        } else { // 
          return (<span class="el-tree-node__label">{node.label}</span>)
        }
      },
      // 
      selTree () {
        var self = this
        var option = {
          url: '/trandef/query',
          success: function (data) {
            if (data.list) {
              self.treeList = (data.list)
              self.treeData = self.formatTreeData(data.list)
            }
          }
        }
        ajax.post(option)
      },
      // Json
      formatTreeData (list, rootNodes) {
        var tree = []
        // fid
        if (rootNodes === undefined || rootNodes.length === 0) {
          rootNodes = []
          for (var i in list) {
            list[i].text = `${list[i].code_value}(${list[i].code_key})`
            if (list[i].fid === undefined || list[i].fid === null || list[i].fid === '') {
              rootNodes.push(list[i])
            }
          }
        }
        // 
        if (rootNodes.length === 0) {
          console.error('')
          console.info('fid')
        }
        // 
        for (var r in rootNodes) {
          var node = rootNodes[r]
          node.children = getChildren(list, node.id)
          tree.push(node)
        }

        // 
        function getChildren (list, id) {
          var childs = []
          for (var i in list) {
            var node = list[i]
            if (node.fid === id) {
              node.children = getChildren(list, node.id)
              // node.icon = 'el-icon-message'
              childs.push(node)
            }
          }
          return childs
        }
        return tree  // Json
      }
    },
    components: {
      'txn-detail': TxnDetail
    }
  }
</script>

<style scoped>
  .alarm-event-query-form-item{
    width: 200px;
  }
</style>



