<template>
  <div>
    <div class="toolbar">
      <el-button plain class="el-icon-plus" @click="addFunc" :disabled="toolBtn.addBtn">
      </el-button>
      <el-button plain class="el-icon-edit" @click="editFunc" :disabled="toolBtn.editBtn">
      </el-button>
      <el-button plain class="el-icon-delete" @click="delFunc" :disabled="toolBtn.delBtn">
      </el-button>
    </div>

    <el-row :gutter="20">
      <el-col :span="6" style="height: 650px;">
        <!--<div style="height:38px;margin-top: 5px;text-align: left;border-bottom: 1px solid #eee;">-->
          <!--<el-button plain class="el-icon-plus" @click="addFunc" :disabled="toolBtn.addBtn" style="margin-left: 5px;">-->
          <!--</el-button>-->
          <!--<el-button plain class="el-icon-edit" @click="editFunc" :disabled="toolBtn.editBtn" style="margin-left: 0px;">-->
          <!--</el-button>-->
          <!--<el-button plain class="el-icon-delete" @click="delFunc" :disabled="toolBtn.delBtn" style="margin-left: 0px;">-->
          <!--</el-button>-->
        <!--</div>-->
        <section class="section">
          <el-tree :data="treeData" node-key="id" ref="tree"
                   :default-expanded-keys="expendKey"
                   :props="defaultProps"
                   :highlight-current=true
                   :expand-on-click-node="false"
                   @node-click="handleNodeClick"
                   :render-content="renderContent">
          </el-tree>
        </section>

      </el-col>
      <el-col :span="18" style="height: 650px;">
        <section class="section">
          <div style="height:38px;width:100%;text-align: left;">
            <el-breadcrumb id="funcPath" separator=">" style="padding: 10px 15px;">
              <el-breadcrumb-item v-for="item in breadcrumbData" :key="item.text">
                {{ item.text }}
              </el-breadcrumb-item>
            </el-breadcrumb>
          </div>
          <el-form :model="funcForm"
                   :rules="rules"
                   ref="funcForm"
                   label-suffix=""
                   :label-width="formLabelWidth"
                   style="text-align: left;"
                   v-bind:class="{hidden:funcFormVisible.form}">
            <el-form-item label="" prop="func_id" v-bind:class="{hidden:funcFormVisible.func_id}">
              <el-input v-model="funcForm.func_id" auto-complete="off"></el-input>
            </el-form-item>
            <el-form-item label="" prop="parent_id" v-bind:class="{hidden:funcFormVisible.parent_id}">
              <el-input v-model="funcForm.parent_id" auto-complete="off"></el-input>
            </el-form-item>
            <el-form-item label="" prop="func_type" v-bind:class="{hidden:funcFormVisible.func_type}">
              <el-input v-model="funcForm.func_type" auto-complete="off"></el-input>
            </el-form-item>
            <el-form-item label="" prop="func_name" v-bind:class="{hidden:funcFormVisible.func_name}">
              <el-input v-model="funcForm.func_name" auto-complete="off" :readonly="funcFormReadonly"></el-input>
            </el-form-item>
            <el-form-item label="" prop="conf" v-bind:class="{hidden:funcFormVisible.conf}">
              <el-input type="textarea" v-model="funcForm.conf" :readonly="funcFormReadonly"></el-input>
            </el-form-item>
            <el-form-item label="" prop="ftype_name" v-bind:class="{hidden:funcFormVisible.ftype_name}">
              <el-input v-model="funcForm.ftype_name" auto-complete="off" :readonly="funcFormReadonly"></el-input>
            </el-form-item>
            <el-form-item label="" prop="isgrant" v-bind:class="{hidden:funcFormVisible.isgrant}">
              <el-checkbox label="" name="type" v-model="funcForm.isgrant"
                           :disabled="funcFormReadonly"></el-checkbox>
            </el-form-item>
            <el-form-item label="" v-bind:class="{hidden:funcFormVisible.menu}">
              <el-switch
                v-model="funcForm.menu"
                active-text=""
                inactive-text=""
                active-value="1"
                inactive-value="0"
                :disabled="funcFormReadonly">
              </el-switch>
            </el-form-item>
            <el-form-item label="" v-bind:class="{hidden:funcFormVisible.flag}">
              <el-switch
                v-model="funcForm.flag"
                active-text=""
                inactive-text=""
                active-value="1"
                inactive-value="0"
                :disabled="funcFormReadonly">
              </el-switch>
            </el-form-item>
            <el-form-item label="" prop="onum" v-bind:class="{hidden:funcFormVisible.onum}">
              <el-input v-model="funcForm.onum" auto-complete="off" :readonly="funcFormReadonly"></el-input>
            </el-form-item>
            <el-form-item label="" prop="info" v-bind:class="{hidden:funcFormVisible.info}">
              <el-input type="textarea" v-model="funcForm.info" :readonly="funcFormReadonly"></el-input>
            </el-form-item>
            <el-form-item label="" prop="islog" v-bind:class="{hidden:funcFormVisible.islog}">
              <el-checkbox label="" name="type" v-model="funcForm.islog" :disabled="funcFormReadonly"></el-checkbox>
            </el-form-item>
            <el-form-item label="" prop="logconf" v-bind:class="{hidden:funcFormVisible.logconf}">
              <el-input v-model="funcForm.logconf" auto-complete="off" :readonly="funcFormReadonly"></el-input>
            </el-form-item>
            <el-form-item v-bind:class="{hidden:funcFormReadonly}">
              <el-button type="primary" @click="submitFunc" size="large"> </el-button>
              <el-button @click="closeFuncForm" size="large"> </el-button>
            </el-form-item>
          </el-form>
        </section>
      </el-col>
    </el-row>
  </div>
</template>


<script>
  import check from '@/common/check'
  import ajax from '@/common/ajax'
  import util from '@/common/util'

  export default {
    created () {
      this.selTree()
    },
    data () {
      // 
      var checkFunctionName = (rule, value, callback) => {
        var funcInfo = this.funcForm
        var treeList = this.treeList
        for (var i in treeList) {
          var funcItem = treeList[i]
          if (funcItem.text === value && funcItem.id !== funcInfo.func_id) {
            return callback(new Error('[]'))
          }
        }
        return callback()
      }
      // 
      var checkFunctionOnum = (rule, value, callback) => {
        var self = this
        if (!(/^(0|[1-9]\d{0,3})$/.test(value))) {
          return callback(new Error('[]100'))
        }
        var currentNode = self.$refs.tree.currentNode.node
        var funcInfo = self.funcForm
        if (funcInfo.func_id === '') { // 
          var childs = currentNode.childNodes
          for (var i in childs) {
            if (childs[i].data.onum === value) {
              return callback(new Error('[]'))
            }
          }
        } else { // 
          var siblings = currentNode.parent.childNodes
          for (var j in siblings) {
            if (siblings[j].data.onum === Number(value) && siblings[j].data.id !== funcInfo.func_id) {
              return callback(new Error('[]'))
            }
          }
        }
        return callback()
      }
      // 
      var checkFunctionLogConf = (rule, value, callback) => {
        var self = this
        if (util.trim(value) === '') {
          return callback()
        }
        if (value.indexOf('GET:') === 0) {
          self.funcForm.logurlMethod = '0'
          self.funcForm.loguri = value.substring(4)
        } else if (value.indexOf('POST:') === 0) {
          self.funcForm.logurlMethod = '1'
          self.funcForm.loguri = value.substring(5)
        } else {
          return callback(new Error('[]GET: POST:'))
        }
      }
      return {
        toolBtn: { // 
          addBtn: true, // 
          editBtn: true, // 
          delBtn: true // 
        },
        treeList: [], // List
        treeData: [{ // Json
          id: '-1',
          func_type: '-1',
          flag: '1',
          ftype_name: '',
          text: '',
          icon: 'ticon-root',
          onum: 0
        }],
        nodeTypes: ['', '', '', ''],
        defaultProps: {
          children: 'children',
          label: 'text'
        },
        expendKey: ['-1'], // id
        formLabelWidth: '120px',
        funcFormReadonly: true, // 
        funcFormVisible: { // 
          form: true, // 
          func_id: true, // id
          parent_id: true, // id
          func_type: true, // 
          func_name: true, // 
          conf: true, // 
          ftype_name: true, // 
          isgrant: true, // 
          menu: true, // 
          flag: true, // 
          onum: true, // 
          info: true, // 
          islog: true, // 
          logconf: true // 
        },
        funcForm: {
          func_id: '', // id
          parent_id: '', // id
          func_type: '', // 
          func_name: '', // 
          conf: '', // 
          ftype_name: '', // 
          isgrant: '', // 
          menu: '1', // 
          flag: '1', // 
          onum: '', // 
          info: '', // 
          islog: '', // 
          logconf: '', // ,
          logurlMethod: '', // 
          loguri: '' // 
        },
        breadcrumbData: [], // 
        rules: { // 
          func_name: [
            {required: true, message: '', trigger: 'blur'},
            {max: 32, message: '32', trigger: 'blur'},
            {validator: check.checkFormSpecialCode, trigger: 'blur'}, // 
            {validator: checkFunctionName, trigger: 'blur'} // 
          ],
          conf: [
            {max: 500, message: '500', trigger: 'blur'}
          ],
          onum: [
            {required: true, message: '', trigger: 'blur'},
            {validator: checkFunctionOnum, trigger: 'blur'} // 
          ],
          info: [
            {max: 300, message: '300', trigger: 'blur'}
          ],
          logconf: [
            {validator: checkFunctionLogConf, trigger: 'blur'} // 
          ]
        }
      }
    },
    methods: {
      // 
      renderContent (h, { node, data, store }) {
        if (node.data.flag === '0') { // 
          return (<span class="el-tree-node__label disabledFlag">{node.label}</span>)
        } else { // 
          return (<span class="el-tree-node__label">{node.label}</span>)
        }
      },
      // 
      selTree () {
        var self = this
        ajax.post({
          url: '/func/tree',
          param: {},
          success: function (data) {
            if (data.list) {
              var rootNodes = { // 
                id: '-1',
                fid: '',
                func_type: '-1',
                flag: '1',
                ftype_name: '',
                text: '',
                icon: 'ticon-root',
                onum: 0
              }
//              var treeList = [rootNodes]
//              treeList = treeList.concat(data.list)
//              self.treeList = treeList
              self.treeData = util.formatTreeData(data.list, [rootNodes])
              self.expendNodesByLevel(2)
            }
          },
          fail: function (e) {
            if (e.response.data.message) {
              self.$message.error(e.response.data.message)
            }
          }
        })
      },
      // 
      expendNodesByLevel (deep) {
        var expendNodeIds = []
        var treeNode = this.treeData[0]
        var level = 1
        while (level <= deep) {
          expendNodeIds.push(treeNode.id)
          treeNode = treeNode.children[0]
          level++
        }
        this.expendKey = expendNodeIds
      },
      // 
      handleNodeClick (data, node) {
        var selectNode = this.funcForm
        // 
        if (selectNode.func_id === data.id) {
          return
        }
        this.$refs['funcForm'].clearValidate()
        var self = this
        var ftype = data.func_type // 
        // 
        if (ftype < '3' && data.flag === '1') {
          self.toolBtn.addBtn = false
        } else {
          self.toolBtn.addBtn = true
        }
        if (ftype === '-1') { // 
          self.toolBtn.editBtn = true
          self.toolBtn.delBtn = true
        } else {
          self.toolBtn.editBtn = false
          self.toolBtn.delBtn = false
        }
        // 
        self.syncBreadcrumb(node)
        // 
        self.funcFormReadonly = true
        self.showNodeAttr(node.data.func_type)
        self.showNodeValue(data)
      },
      // 
      syncBreadcrumb (node) {
        var self = this
        var nodePath = []
        while (node.parent !== null) {
          nodePath.unshift({text: node.data.text})
          node = node.parent
        }
        self.breadcrumbData = nodePath
      },
      // 
      showNodeAttr (ftype) {
        var self = this
        var visible = {
          form: false,
          func_id: true,
          parent_id: true,
          func_type: true,
          func_name: false,
          conf: false,
          ftype_name: false,
          isgrant: false,
          menu: false,
          flag: false,
          onum: false,
          info: false,
          islog: false,
          logconf: false
        }
        if (ftype === '-1') { // 
          visible.ftype_name = true
          visible.isgrant = true
          visible.menu = true
          visible.flag = true
          visible.onum = true
          visible.islog = true
          visible.logconf = true
          self.funcFormVisible = visible
        } else {
          // visible.ftype_name = false
          // visible.flag = false
          // visible.onum = false
          if (ftype === '0' || ftype === '1') {
            // 
            // 
            visible.isgrant = true
            // visible.menu = false
            visible.islog = true
            visible.logconf = true
            self.funcFormVisible = visible
          } else if (ftype === '2') {
            // 
            // , 
            // URL
            // visible.isgrant = false
            // visible.menu = false
            // visible.islog = false
            // visible.logconf = false
            self.funcFormVisible = visible
          } else if (ftype === '3') {
            // 
            // 
            // URL
            visible.isgrant = true
            visible.menu = true
            // visible.islog = false
            // visible.logconf = false
            self.funcFormVisible = visible
          } else {
            // visible.ftype_name = false
            // visible.isgrant = false
            // visible.flag = false
            // visible.onum = false
            self.funcFormVisible = visible
          }
        }
      },
      //  
      showNodeValue (data) {
        var self = this
        var formData = {
          func_id: data.id,
          parent_id: data.fid,
          func_type: data.func_type,
          func_name: data.text,
          conf: data.conf,
          ftype_name: self.nodeTypes[data.func_type],
          isgrant: (data.isgrant === '1'),
          menu: (data.menu ? data.menu : '1'),
          flag: (data.flag ? data.flag : '1'),
          onum: data.onum,
          info: data.info,
          islog: (data.islog === '1'),
          logconf: '', // ,
          logurlMethod: data.logurlmethod, // 
          loguri: data.loguri // 
        }
        switch (data.logurlmethod) {
          case '0' :
            formData.logconf = 'GET:' + data.loguri
            break
          case '1' :
            formData.logconf = 'POST:' + data.loguri
            break
          default :
            break
        }
        self.funcForm = formData
      },
      resetPage () {
        this.funcFormVisible.form = true
        this.breadcrumbData = []
        this.toolBtn = { // 
          addBtn: true, // 
          editBtn: true, // 
          delBtn: true // 
        }
      },
      // 
      addFunc () {
        this.$refs['funcForm'].resetFields()
        var self = this
        var selectNode = self.$refs.tree.getCurrentNode()
        var funcType = Number(selectNode.func_type) + 1
        self.funcForm.parent_id = selectNode.id
        self.funcForm.func_type = funcType
        self.funcForm.ftype_name = self.nodeTypes[funcType]
          // 
        self.funcFormReadonly = false
        self.showNodeAttr(funcType + '')
      },
      // 
      editFunc () {
        // 
        this.funcFormReadonly = false
      },
      // 
      delFunc () {
        var self = this
        var selectNode = self.$refs.tree.currentNode.node
        this.$confirm('', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          ajax.post({
            url: '/func/del',
            param: {
              funcId: selectNode.data.id,
              rf: 'json'
            },
            success: function (data) {
              selectNode.visible = false
              self.resetPage()
              self.$message.success('')
            },
            fail: function (e) {
              if (e.response.data.message) {
                self.$message.error(e.response.data.message)
              }
            }
          })
        }).catch(() => {})
      },
      closeFuncForm () {
        var self = this
        if (self.funcForm.func_id === '') {
          self.resetPage()
        } else {
          self.funcFormReadonly = true
        }
      },
      submitFunc () {
        var self = this
        this.$refs['funcForm'].validate((valid) => {
          if (valid) {
            let url = ''
            let opStr = ''
            if (self.funcForm.func_id === '') {
              url = '/func/add'
              opStr = ''
            } else {
              url = '/func/mod'
              opStr = ''
            }
            ajax.post({
              url: url,
              param: self.funcForm,
              success: function (data) {
                self.$message.success(opStr + '')
                self.selTree()
                self.resetPage()
              },
              fail: function (e) {
                if (e.response.data.message) {
                  self.$message.error(e.response.data.message)
                }
              }
            })
          }
        })
      }
    }
  }
</script>

<style>
  .hidden {
    display: none;
  }
  .disabledFlag {
    color: #dcdfe6;
  }
</style>



